body.home
{
	padding-top:0;

	.home-header-col
	{
		//padding:0;
	}

	.home-cover
	{
		background-image:url('../img/photos/default_home_cover.jpg');
		background-position:50% 50%;
		background-size:cover;

		margin-bottom:(2 * @line-height-computed);

		@media (orientation:portrait)
		{
			background-image:url('../img/photos/default_home_cover.jpg');
		}

		.home-cover-col
		{
			min-height:100vh;
			padding-top:@navbar-height;

			display:flex;
			flex-direction:column;
			justify-content:space-between;
			align-items:center;

			.title-section
			{
				padding-top:@line-height-computed;

				h1
				{
					text-align:center;
					color:@gray-lighter;
					text-shadow:2px 2px 0 #000,-1px -1px 0 #aaa;
					//font-family:@font-family-headings;

					@media (max-width:@screen-xxs-max)
					{
						font-size:28px;
					}
				}

				img
				{
					display:block;
					margin-left:auto;
					margin-right:auto;

					width:100%;
					min-width:140px;
					max-width:280px;
				}
			}

			.subtitle-section
			{
				padding-bottom:(2 * @line-height-computed);
				padding-left:ceil((@grid-gutter-width / 2));
				padding-right:floor((@grid-gutter-width / 2));

				h2
				{
					text-align:center;
					//font-family:@font-family-headings;
					color:@gray-lighter;
					text-shadow:1px 1px 0 #000,-1px -1px 0 #aaa;

					@media (max-width:@screen-xxs-max)
					{
						font-size:24px;
					}
				}

				.links
				{
					display:flex;
					justify-content:space-around;
					align-items:center;

					@media (orientation:portrait)
					{
						flex-direction:column;
					}

					a
					{
						display:inline-block;

						margin:@line-height-computed;
						padding:ceil((@line-height-computed / 2)) @line-height-computed;
						border:2px solid @gray-lighter;

						//font-family:@font-family-headings;
						font-size:24px;
						color:@gray-lighter;
						background-image:linear-gradient(45deg, rgba(0,0,0,.5) 0, rgba(0,0,0,.8) 50%, rgba(0,0,0,.5) 100%);

						text-align:center;

						&:hover, &:focus
						{
							box-shadow:0 0 6px 0 @white;
							text-decoration:none;
						}

						@media (orientation:portrait)
						{
							width:100%;
							margin:@line-height-computed 0;
						}

						@media (max-width:@screen-xxs-max)
						{
							font-size:20px;
						}
					}
				}
			}
		}
	}

	.user-form-layer
	{
		form
		{
			padding:ceil((@grid-gutter-width / 2 ));

			background-color:@brand-primary;
			border:2px solid @brand-secondary;
			border-radius:ceil((@grid-gutter-width / 2 ));

			box-shadow:2px 2px 2px 0 #666;

			button[type="submit"]
			{
				display: block;
				margin: 0 auto;
			}
		}
	}

	.main-footer
	{
		margin-top:@line-height-computed;
	}
}
