body.trainer-layout
{
	background-image: repeating-linear-gradient(30deg, rgba(255,255,255,.1) 0, rgba(255,255,255,.3) 14%), linear-gradient(45deg,#d4d237 0,#e6e489 100%);

	padding-top:0;

	.settings-menu
	{
		position:fixed;
		right:0;
		top:50px;
		z-index:1000;

		width:max-content;

		.settings-menu-col
		{
			a
			{
				max-width:36px;
			}
		}
	}

	.settings-menu2
	{
		float: right;
		background-color: #fff;
		padding: 4px 4px 0 4px;
		border-radius: 60px;
		margin-top: 4px;
		margin-bottom: 4px;
		box-shadow: inset 1px 1px 2px 0 #666;
		background-image: linear-gradient(to top, #d8d8d8 0, #fff 100%);

		a{/*dent around button*/
			position: relative;
			display: inline-block;
			width: 40px;
			height: 40px;
			color: #d8d54b;
			text-align: center;
			text-decoration: none;
			background: #fff;
			border-radius: 50%;
			box-shadow: inset 0 0 4px rgba(0,0,0,.08);
			margin: 0 4px;
		}
		a .fa {/*Button itself*/
			position: absolute;
			top: 0;
			left: 0;
			width: 40px;
			height: 40px;
			font-size: 24px;
			line-height: 40px;
			text-shadow: 1px 1px 1px rgba(255,255,255,.66);
			vertical-align: middle;
			content: '';
			background-image: linear-gradient(#e8e8e8 0,#d6d6d6 100%);
			border-bottom: solid 2px #b5b5b5;
			border-radius: 50%;
			box-shadow: inset 0 2px 0 rgba(255,255,255,.5), 0 2px 2px rgba(0,0,0,.19);
		  }
		  
		a .fa:active{
			background-image: -webkit-linear-gradient(#efefef 0%, #d6d6d6 100%);
			box-shadow: inset 0 1px 0 rgba(255,255,255,0.5), 0 2px 2px rgba(0, 0, 0, 0.19);
			border-bottom: solid 2px #d8d8d8;
		}
	}

	.container-trainer
	{
		.section-header-row
		{
			margin-bottom:30px;
			margin-bottom: 30px;
			color: #dcda5c;
			background-color: #ececec;
			border-bottom: 2px solid #b9b73d;
			box-shadow: 0 2px 2px 0 #666;
			background-image: linear-gradient(to top, #ccc 0, #fff 100%);

			h1.main-title
			{
				margin-top:10px;
				font-family:@font-family-headings;

				color: #3c3c3c;
				text-shadow: 1px 1px 0 #dbd958, -1px -1px 0 #fff;
			}
		}

		.container.content
		{
			min-height:80vh;

			background-color:#fff;

			box-shadow:inset 2px 2px 2px 0 #aaa;	

			padding-top:15px;
			padding-bottom:15px;

			margin-bottom:20px;

			.tab-pane
			{
				padding-top:20px;
			}

			.exercises
			{
				display:flex;
				flex-wrap:wrap;
				justify-content:flex-start;

				.create-new
				{
					margin-bottom:20px;
					margin-left:1.6666%;
					margin-right:1.6666%;
					width:30%;

					cursor:pointer;

					&:hover
					{
						box-shadow:0 0 2px 0 @brand-primary;
					}

					.images
					{
						width:100%;

						position:relative;

						button
						{
							width:100%;
							border:0;
							background-color:transparent;
							
							img
							{
								width:100%;
								height:auto;
							}
						}
						
					}

					.title
					{
						text-align:center;
					}
				}

				.exercise
				{
					margin-bottom:20px;
					margin-left:1.6666%;
					margin-right:1.6666%;
					width:30%;

					cursor:pointer;

					&:hover
					{
						box-shadow:0 0 2px 0 @brand-primary;
					}

					&.inactive
					{
						opacity:.5;
					}

					.images
					{
						width:100%;
						padding-bottom:100%;

						position:relative;

						.image-start, .image-end
						{
							position:absolute;
							top:0;
							left:0;
							width:100%;
							height:100%;

							background-size:contain;
							background-repeat:no-repeat;
							background-position:50% 50%;

							transition:opacity 1s ease;
						}

						.image-end
						{
							opacity:0;
						}

						&.turned
						{
							.image-start
							{
								opacity:0;
								transition:opacity 1s ease;
							}
							.image-end
							{
								opacity:1;
								transition:opacity 1s ease;
							}
						}
					}

					.title
					{
						text-align:center;
					}
				}
			}

		}
	}

	.exercise-card
	{
		.video-col
		{
			padding-top:10px;
			padding-bottom:10px;

			iframe
			{
				max-width:100%;
				margin-left:auto;
				margin-right:auto;
				display:block;
			}
		}

		.description-col
		{
			padding-top:20px;
			padding-bottom:20px;
		}
	}

}
