.site-style-content
{
	.logos-col
	{
		display:flex;
		flex-wrap:wrap;
		align-items:center;

		.layer
		{
			width:50%;
			@media (max-width:@screen-xs-max) {
				width:100%;

				&.big-logo-layer {order:1};
				&.small-logo-layer {order:3};
				&.big-logo-field-layer {order:2};
				&.small-logo-field-layer {order:4};
			}

			.form-group
			{
				width:max-content;
				margin-left:auto;
				margin-right:auto;
			}

			.current-image
			{
				text-align:center;

				img
				{
					max-width:100%;
					height:auto;
				}	
			}
		}
	}
}
