.page-content
{
	padding-bottom:@line-height-computed;
}


.settings-menu .settings-menu-col
{
	display:flex;
	justify-content:center;
	padding-top:10px;
	padding-bottom:10px;
}

.settings-menu .settings-menu-col a
{
	border:2px solid @brand-primary;
	border-radius:12px;

	margin-left:5px;
	margin-right:5px;
	max-width:60px;

	display:block;

	box-shadow: 1px 1px 0 0 #666, -1px -1px 0 0 #aaa;
	background-image:radial-gradient(circle at center, lighten(@brand-primary,15%) 0, @brand-primary 100%);
}
.settings-menu .settings-menu-col a:hover
{
	box-shadow:0 0 3px 0 @brand-primary;
	border:2px solid @brand-secondary;
	background-image:radial-gradient(circle at center, lighten(@brand-secondary,15%) 0, @brand-secondary 100%);
}

.settings-menu .settings-menu-col svg
{
	width:100%;
	height:auto;
}

.settings-menu .settings-menu-col svg path
{
	stroke:@brand-secondary !important;
}
.settings-menu .settings-menu-col a:hover svg path
{
	stroke:@brand-primary !important;
}


.trainer-settings-content
{
	.links
	{
		display:flex;
		flex-wrap:wrap;
		justify-content:space-between;

		margin-top:20px;
		margin-bottom:20px;

		a
		{

			width:32%;

			display:block;
			margin-bottom:20px;


			.image
			{
				border:2px solid @brand-primary;
				border-radius:20px;

				box-shadow: 1px 1px 0 0 #666, -1px -1px 0 0 #aaa;
				background-image:radial-gradient(circle at center, lighten(@brand-primary,15%) 0, @brand-primary 100%);
				svg
				{
					width:100%;
					height:auto;

					path
					{
						stroke:@brand-secondary !important;
					}
				}
			}

			.text
			{
				padding-top:10px;

				p
				{
					text-align:center;
					color:@brand-primary;
				}
			}

			&:hover
			{
				text-decoration:none;

				.image
				{
					box-shadow:0 0 3px 0 @brand-primary;
					border:2px solid @brand-secondary;
					background-image:radial-gradient(circle at center, lighten(@brand-secondary,15%) 0, @brand-secondary 100%);

					svg
					{
						path
						{
							stroke:@brand-primary !important;
						}
					}
				}

				.text
				{
					p
					{
						color:@brand-secondary;
					}
				}
			}
		}
	}
}
