.basic-contact-layer()
{
	//background-color:#fff;
	//background-repeat:no-repeat;
	//background-position:50% 0;
	
	//box-shadow:0 -2px 4px 0 @gray;

	& > .row
	{
		background-color:@background-color-2;
		#gradient .vertical(darken(@background-color-2,8%),@background-color-2);

		& > .col-xs-12
		{
		//	padding-bottom:@line-height-computed;
		}
	}

	.contact-flex-row
	{
		display:flex;
		flex-wrap:wrap;

		.contact
		{
			display:flex;
			flex-direction:column;
			align-items:center;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				order:2;
				padding:0;
			}
		}

		.img
		{
			display:flex;
			align-items:flex-end;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				img
				{
					width:100%;
					max-width:300px;
					margin-bottom:(@line-height-computed / 2);
				}

				order:1;
			}
		}
	}

	h1,h2,h3
	{
		//color:@white;
		//text-shadow:1px 1px 0 #aa8f7d; 
	}

	form
	{
		width:100%;
		padding:(@line-height-computed / 2);
		background-color:@brand-primary;
		box-shadow:2px 2px 0 0 @brand-secondary;

		label
		{
			color:@white;
			//text-shadow:1px 1px 0 #aa8f7d; 
		}

		input, textarea
		{
			border-radius:0;
			
		}

		a
		{
			color:@white;
			font-weight:bold;

			&:hover
			{
				//color:lighten(@brand-primary,30%);
			}
		}

		button
		{
			//margin-top:10px;
			//width:100%;
			//.btn-styles(@background-color-2);
			//color:@white;

			display:block;
			margin:@line-height-computed auto;
			font-size:24px;
			.btn-styles(#21b72e);

			&:hover, &:focus, &:active
			{
				//color:@white;
			}
		}

	}

	.map-col
	{
		padding:0;
		
		iframe
		{
			width:100%;
		}
	}
	
}

.contact-container
{
	.basic-contact-layer();

}


